import axios from 'axios'
import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
const StraydogCreate = () => {

    //        .

    const [Sex, setSex] = useState(null)
    const changeSex = event => {
        setSex(event.target.value);
        console.log(event.target.value);
    };
    const [Age, setAge] = useState(null)
    const changeAge = event => {
        setAge(event.target.value);
        console.log(event.target.value);
    };
    const [ChipNumber, setChipNumber] = useState(null)
    const changeChipNumber = event => {
        setChipNumber(event.target.value);
        console.log(event.target.value);
    };
    const [Image, setImage] = useState(null)
    const changeImage = event => {
        setImage(event.target.value);
        console.log(event.target.value);
    };
    const [Breed, setBreed] = useState(null)
    const changeBreed = event => {
        setBreed(event.target.value);
        console.log(event.target.value);
    };
    const [RemainedDay, setRemainedDay] = useState(null)
    const changeRemainedDay = event => {
        setRemainedDay(event.target.value);
        console.log(event.target.value);
    };
    const [DogSize, setDogSize] = useState(null)
    const changeDogSize = event => {
        setDogSize(event.target.value);
        console.log(event.target.value);
    };
    const [Weight, setWeight] = useState(null)
    const changeWeight = event => {
        setWeight(event.target.value);
        console.log(event.target.value);
    };
    const [Status, setStatus] = useState(null)
    const changeStatus = event => {
        setStatus(event.target.value);
        console.log(event.target.value);
    };
    const [EnteredDay, setEnteredDay] = useState(null)
    const changeEnteredDay = event => {
        setEnteredDay(event.target.value);
        console.log(event.target.value);
    };
    const [DiscoveredPlace, setDiscoveredPlace] = useState(null)
    const changeDiscoveredPlace = event => {
        setDiscoveredPlace(event.target.value);
        console.log(event.target.value);
    };
    // 
    // const [LostLocation, setLostLocation] = useState(null)
    // const changeLostLocation = event => {
    //     setLostLocation(event.target.value);
    //     console.log(event.target.value);
    //   };
    // const [LostDate, setLostDate] = useState(null)
    // const changeLostDate = event => {
    //     setLostDate(event.target.value);
    //     console.log(event.target.value);
    //   };
    // const [ReturnedHome, setReturnedHome] = useState(null)
    // const changeReturnedHome = event => {
    //     setReturnedHome(event.target.value);
    //     console.log(event.target.value);
    //   };


    // =====================================================
    const navigate = useNavigate()
    // CREATE    
    const Create = (e) => {
        // e.preventDefault();

        //     
        if (Sex === null || Sex === '') {
            alert(' ')
        } else if (Age === null) {
            alert('  ')
        } else if (Image === null || Image === '') {
            alert(' ')
        } else if (Breed === null || Breed === '') {
            alert(' ')
        } else if (RemainedDay === null) {
            alert('  ')
        } else if (DogSize === null || DogSize === '') {
            alert(' ')
        } else if (Weight === null) {
            alert(' ')
        } else if (EnteredDay === null) {
            alert('  ')
        } else if (DiscoveredPlace === null || DiscoveredPlace === '') {
            alert('  ')
        }

        else {
            // e.preventDefault();
            axios.post('/api/straydog', JSON.stringify(
                {
                    "Sex": Sex,
                    "Age": Age,
                    "ChipNumber": ChipNumber,
                    "Image": Image,
                    "Breed": Breed,
                    "RemainedDay": RemainedDay,
                    "DogSize": DogSize,
                    "Weight": Weight,
                    "Status": "stray",
                    "EnteredDay": EnteredDay,
                    "DiscoveredPlace": DiscoveredPlace,
                    "LostLocation": null,
                    "LostDate": null,
                    "ReturnedHome": null
                }), { headers: { "Content-Type": 'application/json' } })
                .then(function (response) {
                    console.log(response);
                    navigate('/straydog')
                })
                .catch(function (error) {
                    console.log(error);
                });


        }
        // POST  

        // =====================================================
    }


    return (
        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', paddingTop: '200px' }}>
            <h1>
                   
            </h1>
            <hr />
            <div style={{ display: "flex" }}>
                <div >
                    {/* <div> */}
                    {/* <label htmlFor='sex'>   </label><input id='sex' type="text" onChange={changeSex}/> */}
                    {/* </div> */}
                    <div onChange={changeSex}>
                         
                        <input type="radio" name="theme" value="Male" id="male" /><label htmlFor="male"></label>
                        <input type="radio" name="theme" value="Female" id="female" /><label htmlFor="female"></label>
                    </div>
                    <div>
                        <label htmlFor='age'>   </label><input id='age' type="number" onChange={changeAge} />
                    </div>
                    <div>
                        <label htmlFor='chip_number'>  </label><input id='chip_number' type="text" onChange={changeChipNumber} />
                    </div>
                    <div>
                        <label htmlFor='image'>   </label><input id='image' type="text" onChange={changeImage} />
                    </div>
                    <div>
                        <label htmlFor='breed'>   </label><input id='breed' type="text" onChange={changeBreed} />
                    </div>



                </div>
                <div>
                    <div onChange={changeDogSize}>
                        
                        <input type="radio" name="size" value="small" id="small" /><label htmlFor="small"></label>
                        <input type="radio" name="size" value="medium" id="medium" /><label htmlFor="medium"></label>
                        <input type="radio" name="size" value="large" id="large" /><label htmlFor="large"></label>
                    </div>
                    <div>
                        <label htmlFor='weight'>   </label><input id='weight' type="number" onChange={changeWeight} />
                    </div>
                    <div>
                        <label htmlFor='status'>   </label><input id='status' value="stray" type="text" />
                    </div>

                </div>
            </div>
            <hr />
            {/* STRAY */}
            <div>
                <label htmlFor='entered_day'>   </label><input id='entered_day' type="date" min="2000-01-01" max="2100-12-31" onChange={changeEnteredDay} />
                <label htmlFor='discovered_place'>   </label><input id='discovered_place' type="text" onChange={changeDiscoveredPlace} />
                <label htmlFor='remained_day'>   </label><input id='remained_day' type="number" onChange={changeRemainedDay} />
            </div>

            {/* LOST */}
            {/*      .
            <div>
                <label htmlFor='lost_location'>    </label><input id='lost_location' type="text" onChange={changeLostLocation}/>
                <label htmlFor='lost_date'>   </label><input id='lost_date' type="text" onChange={changeLostDate}/>
                <label htmlFor='returned_home'>   </label><input id='returned_home' type="text" onChange={changeReturnedHome}/>
            </div> */}


            {/* <input type="submit" /> */}
            {/* <input type="submit" style={{width : '100px', height : '100px'}} value="" OnClick={Create}></input> */}
            <hr />

            <button onClick={Create} style={{ width: '100px', height: '100px' }}> </button>
        </div>
    );
};
export default StraydogCreate